<template>
	<view class="flex flex-direction">
		<!-- 导航栏 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">个人信息</block>
		</cu-custom>
		<!-- 登录判断 -->
		<is-login currentPage="index"></is-login>
		<!-- 表单 -->
		<uni-forms :rules="rules" :value="current" ref="form" validate-trigger="bind" err-show-type="undertext">
			<!-- 头像 -->
			<view class="bg-white flex solid-bottom padding justify-center">
				<image class="cu-avatar round xl margin-left flex-item"
					:src="current.Avatar?current.Avatar:'/static/login/logo_my.png'" @tap="changeAvatar"></image>
			</view>
			<!-- 用户名 -->
			<uni-forms-item name="LoginName">
				<view class="cu-form-group solid-bottom">
					<view class=" title text-bold">用户名</view>
					<input class=" text-grey text-left" :value="current.LoginName" disabled></input>
				</view>
			</uni-forms-item>
			<!-- 名称 -->
			<uni-form-item name="Name" required>
				<view class="cu-form-group solid-bottom">
					<view class="title text-bold">名称</view>
					<input placeholder="请输入名称" :value="current.Name"
						@input="changeName($event);binddata('Name', $event.detail.value,'form');" />
				</view>
			</uni-form-item>
			<!-- 手机号 -->
			<uni-forms-item name="Mobile">
				<view class="cu-form-group solid-bottom">
					<view class="title text-bold">手机号</view>
					<input placeholder="请输入手机号码" type="number" :value="current.Mobile"
						@input="changeMobile($event);binddata('Mobile' ,$event.detail.value , 'form')" />
				</view>
			</uni-forms-item>
			<!-- 邮箱 -->
			<uni-forms-item name="Email">
				<view class="cu-form-group solid-bottom">
					<view class="title text-bold">邮箱</view>
					<input placeholder="请输入邮箱号" :value="current.Email"
						@input="changeEmail($event);binddata('Email',$event.detail.value , 'form')" />
				</view>
			</uni-forms-item>
			<!-- 创建日期 -->
			<view class="cu-form-group">
				<view class="title text-bold" style="width: 200rpx;">创建日期</view>
				<uni-dateformat class="text-left text-grey padding-tb-sm " style="width: 100%;"
					:date="current.CreatedAt" :threshold="[60000, 3600000]"></uni-dateformat>
			</view>
		</uni-forms>
		<button class="cu-btn bg-cyan padding round margin-lr margin-top" @click="save('form')">保存</button>
		<navigator v-if="isCurrent" class="flex flex-direction" url="/pages/basic/my/changepassword?preview=true">
			<text class="cu-btn text-blue  padding  margin ">修改密码</text>
		</navigator>
	</view>
</template>

<script>
	/** 用户详细信息
	 *  @author 陆彦捷
	 * @date 2021.06.18
	 * @date 2021.07.20 改为引用公共方法，上传头像
	 * @date 2021.07.23 重新引用login
	 * @description 查看用户的详细信息。
	 * 注：绑定微信和解绑微信的方法，暂时没有
	 * 注: 名称验证无效
	 */
	import user from '@/common/storage/userstorage.js';				// 用户信息
	import publicfunc from '@/common/function/publicfunc.js';		// 公共方法
	
	var _self;
	export default {
		data() {
			return {
				// 样式相关组件
				preview: false, // 是否查看上一页
				isCurrent: false, // 是否可用修改当前用户密码
				// 系统相关参数
				UserId: 0, // 用户id
				Token: this.global.login.getToken(), // 当前Token
				current: {},
				Avatar_init: null, // 初始时头像
				rules: {
					Avatar: {
						rules: []
					}, // 头像
					LoginName: {
						rules: []
					}, // 用户名
					Name: {
						rules: [{
							required: true,
							errorMessage: '请输入名称'
						}]
					}, // 名称
					Mobile: {
						rules: [{
								required: true,
								errorMessage: '请输入手机号'
							},
							{
								pattern: "^[0-9]{11}$",
								errorMessage: '手机号为11位数'
							},
							{
								pattern: "^1[3|5|6|7|8|9][0-9]{9}$",
								errorMessage: '请输入正确的手机号码'
							}
						]
					}, // 手机号
					Email: {
						rules: [{
								required: true,
								errorMessage: '请输入邮箱'
							},
							{
								format: 'email',
								errorMessage: '请输入正确的邮箱地址'
							}
						]
					}, // 邮箱
				},
			}
		},
		onReady() {
			// 需要在onReady中设置规则
			this.$refs.form.setRules(this.rules);
		},
		onLoad: function(options) {
			_self = this;
			this.getParams(options);
			this.userInfo_request(); // 获取当前用户信息
		},
		methods: {
			// ============================================ 初始化 ==============================================
			/** 获取初始化参数
			 * @param {Object} options
			 */
			getParams: function(options) {
				this.preview = options.preview != 'false' ? Boolean(options.preview ) : false;
				this.UserId = options.UserId ? options.UserId : this.global.login.getUserId();
				this.isCurrent = !options.UserId; // 是否为当前用户
			},
			// ============================================ 监听事件 ==============================================
			/** 修改头像
			 * @param {Object} e
			 */
			changeAvatar: function(e) {
				// 显示本地上传的头像
				uni.chooseImage({
					count: 1, // 最多可选择图片张数
					success: function(result) {
						// 直接修改本地预览
						if (result.tempFilePaths.length == 1) {
							_self.current.Avatar = result.tempFilePaths[0];
						}
					},
					fail: function(erros) {
						uni.showToast({
							title: '选择图片失败！',
							icon: 'none'
						});
					}
				});
				// uni.show
			},
			/** 修改名称
			 * @param {Object} e
			 */
			changeName: function(e) {
				this.current.Name = e.detail.value;
			},
			/** 修改手机号
			 * @param {Object} e
			 */
			changeMobile: function(e) {
				this.current.Mobile = e.detail.value;
			},
			/**修改邮箱地址
			 * @param {Object} e
			 */
			changeEmail: function(e) {
				this.current.Email = e.detail.value;
			},
			/**
			 * 保存数据（个人信息）
			 * @param {string} 提交表单名称 
			 */
			save: function(form) {
				this.$refs[form].validate()
					.then((result) => {
						// 判断图片是否为初始化图片
						uni.showModal({
							title:'保存',
							content:'是否保存对当前用户信息的修改？',
							success:function(result){
								if(result.confirm){
									//点击确定，保存当前的用户信息
									if (_self.current.Avatar === _self.Avatar_init) {
										_self.changeUser_request();
									} else {
										publicfunc.imgUpload(_self.current.Avatar , '头像上传中……' , _self.imgUpload_success , _self.imgUpload_fail , _self.changeUser_request);
									}
								}
							}
						});
					})
					.catch((erros) => {
						// 显示错误信息
						uni.showToast({
							title: erros[0].errorMessage,
							icon: 'none'
						});
					});
			},
			// =============================================== 公共方法 ==========================================================
			/** 头像上传成功后执行方法
			 * @param {Object} ImgUrl 图像返回路径
			 */
			imgUpload_success : function(ImgUrl){
				this.current.Avatar = ImgUrl;
			},
			/** 头像上传失败后执行方法
			 * @param {Object} errors
			 */
			imgUpload_fail : function(errors){
				console.log("头像上传失败执行方法");
				if(Boolean(errors)){
					console.log("头像上传fail=>" , errors);
				}
				this.current.Avatar = this.Avatar_init;
			},
			// =============================================== 请求方法 ==========================================================
			/**
			 * 获取当前用户信息
			 */
			userInfo_request: function() {
				uni.showLoading();
				uni.request({
					url: _self.website + '/api/SvcUser/Info/' + _self.UserId,
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'GET',
					success: function(result) {
						uni.hideLoading();
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0") {
								var data = result.data.Data;
								if (data) {
									_self.current = data;
									_self.Avatar_init = data.Avatar;
								}
								return;
							}
						}
						uni.showToast({
							title: '没有正确的返回数据'
						});
					},
					fail: function(erros) {
						uni.hideLoading();
						uni.showToast({
							title: '服务器连接失败！',
							icon: 'none'
						});
					}
				})
			},
			/**
			 * 修改用户信息
			 */
			changeUser_request: function() {
				console.log("修改用户信息");
				uni.showLoading({
					title:'修改信息请求中，请耐心等待……'
				});
				uni.request({
					url: _self.website + '/api/SvcUser/Update',
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'POST',
					data: _self.current,
					success: function(result) {
						uni.hideLoading();
						if (result.statusCode == 200) {
							uni.showToast({
								title: result.data.ResultMsg,
								icon: 'none'
							});
							// 如果是当前用户，修改当前Session
							if (result.data.ResultCode == "0" && _self.isCurrent) {
								var data = result.data.Data;
								user.setName(data.Name);
								user.setLoginName(data.LoginName);
								user.setMobile(data.Mobile);
								user.setAvatar(data.Avatar);
							}
							return;
						}
						uni.showToast({
							title: '修改用户信息失败',
							icon: 'none'
						});
						console.log("success:修改用户信息失败=>", result);
					},
					fail: function(erros) {
						uni.hideLoading();
						console.log("fail:修改用户信息失败=>", erros);
						uni.showToast({
							title: '连接服务器失败！',
							icon: 'none'
						});
					}
				});
			}
			// =================================================================================================
		}
	}
</script>

